.task-item-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 825px;
  height: 75px;
}
.task-item-container .text-content {
  padding: 0px;
  height: 75px;
}
.task-item-container .text-content .task-card {
  display: flex;
  flex: 1fr 1fr 1fr 1fr;
  justify-content: space-between;
}
.task-item-container .text-content .task {
  width: 725px;
  height: 75px;
  align-content: center;
}
.task-item-container .actions {
  display: grid;
  grid-template-columns: 1fr 1fr;
  height: 75px;
  width: 175px;
  justify-content: center;
  align-content: center;
}
.task-item-container .actions .button {
  background-color: #eeeeee;
  border: grey 1px solid;
  display: grid;
  width: 62px;
  height: 75px;
  justify-content: center;
  align-items: center;
}
.task-item-container .actions .button .action-icon {
  display: grid;
  color: black;
  padding: 0px;
  height: 50%;
  cursor: pointer;
}
.task-item-container .actions .edit-task-form {
  display: none;
}
.task-item-container .actions .content {
  display: none;
}
.task-item-container .actions .show {
  display: block;
}