.nav-wrapper {
  display: grid;
  grid-template-columns: 1fr;
  font-size: 20px;
  justify-content: center;
  align-items: center;
  width: 1500px;
}
.nav-wrapper .nav-link-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  justify-content: center;
  justify-items: center;
  width: 1500px;
  height: 200px;
}
.nav-wrapper .nav-link-wrapper .main-login-wrapper {
  display: grid;
  grid-template-rows: 1fr;
  justify-items: center;
  align-items: center;
  width: 100%;
  height: 150px;
}
.nav-wrapper .nav-link-wrapper .main-login-wrapper .login {
  display: grid;
  justify-content: center;
  align-items: center;
}
.nav-wrapper .nav-link-wrapper .scan,
.nav-wrapper .nav-link-wrapper .search,
.nav-wrapper .nav-link-wrapper .manager {
  display: grid;
  align-items: center;
  justify-content: center;
  justify-items: center;
  height: 80%;
  width: 80%;
  cursor: pointer;
}
.nav-wrapper .nav-link-wrapper .scan .icon,
.nav-wrapper .nav-link-wrapper .search .icon,
.nav-wrapper .nav-link-wrapper .manager .icon {
  font-size: 125px;
  display: grid;
  justify-content: center;
}
.nav-wrapper .login-container {
  display: block;
  width: 300px;
  justify-content: center;
  align-items: center;
}
.nav-wrapper .login-container .login-wrapper {
  display: grid;
  grid-template-rows: 1fr 1fr;
  width: 300px;
  justify-content: center;
}
.nav-wrapper .login-container .login-wrapper .hide {
  display: none;
}
.nav-wrapper .login-container .login-wrapper .login-box {
  display: grid;
  justify-content: center;
  align-items: center;
}
.nav-wrapper .login-container .login-wrapper .login-box .button-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
}