.Specs-item-container {
  display: grid;
  grid-template-columns: 7fr 1fr;
  padding: 30px;
  justify-content: center;
  justify-items: center;
  align-items: center;
  width: 1600px;
}
.Specs-item-container .spacer {
  width: 60px;
}
.Specs-item-container .text-content {
  height: 100%;
  width: 100%;
  padding: 30px;
  justify-items: left;
  justify-content: center;
}
.Specs-item-container .text-content .item-card {
  display: grid;
  justify-content: center;
  grid-template-columns: 1fr 1fr;
  height: 5em;
}
.Specs-item-container .text-content .item-card .leftside {
  display: grid;
  grid-template-rows: 1fr 1fr;
}
.Specs-item-container .text-content .item-card .leftside .top {
  height: 325px;
  justify-content: center;
  align-items: center;
}
.Specs-item-container .text-content .item-card .leftside .top img {
  width: auto;
  height: 90%;
}
.Specs-item-container .text-content .item-card .leftside .bottom {
  height: 309px;
  justify-content: center;
  align-content: bottom;
}
.Specs-item-container .text-content .item-card .leftside .bottom img {
  width: 100%;
  height: auto;
}
.Specs-item-container .text-content .item-card .rightside {
  display: grid;
  grid-template-columns: 1fr 1fr;
  border-style: solid;
  height: 627px;
  width: 800px;
  font-size: 500%;
}
.Specs-item-container .text-content .item-card .rightside .left .item {
  justify-content: space-between;
  margin: 0px 25px 50px 0px;
}
.Specs-item-container .text-content .item-card .rightside .right .item {
  justify-content: space-between;
  margin: 0px 25px 50px 0px;
}
.Specs-item-container .actions {
  display: grid;
  grid-template-columns: 1fr;
  height: 620px;
  justify-items: center;
  align-items: baseline;
}
.Specs-item-container .actions .disabled {
  pointer-events: none;
  opacity: 0.5;
}
.Specs-item-container .action-icon {
  font-size: 1000%;
  padding: 50px;
}
.Specs-item-container .record-link {
  font-size: 700%;
}