.task-container {
  display: grid;
  grid-template-rows: 1fr 1fr 1fr;
  height: 200px;
}
.task-container .set-task-form {
  display: grid;
  grid-template-rows: 1fr 1fr;
  height: 300px;
}
.task-container .set-task-form h1 {
  height: 30px;
}
.task-container .set-task-form form {
  display: grid;
  grid-template-rows: 1fr 1fr;
  height: 200px;
}
.task-container .set-task-form form .Set-task-Form {
  display: grid;
  grid-template-rows: 1fr 10fr;
  height: 125px;
}
.task-container .set-task-form form .Set-task-Form .upper-input-container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
  height: 35px;
}
.task-container .set-task-form form .Set-task-Form select {
  height: 100%;
}
.task-container .set-task-form form .Set-task-Form select .hdselector {
  width: 80px;
}
.task-container .set-task-form form .Set-task-Form input {
  height: 100%;
}
.task-container .set-task-form form .Set-task-Form .startedat {
  height: 100%;
}
.task-container .set-task-form form .Set-task-Form .startedat .start {
  display: none;
}
.task-container .set-task-form form .Set-task-Form .startedat .show {
  display: block;
  width: 150px;
}
.task-container .set-task-form form .Set-task-Form textarea {
  height: 100px;
}