.wrapper {
  display: grid;
}
.wrapper .tasklist-wrapper {
  display: grid;
  grid-template-rows: 1fr 1fr 1fr;
  gap: 20px;
  padding: 20px;
  height: 50px;
}
.wrapper .tasklist-wrapper .id-container {
  display: grid;
  grid-template-columns: 1fr 10fr;
  font-weight: bold;
}
.wrapper .tasklist-wrapper .job-container {
  display: grid;
  grid-template-columns: 1fr 10fr;
  font-weight: bold;
}
.wrapper .tasklist-wrapper .instructions-container {
  font-weight: bold;
  display: grid;
  grid-template-rows: 1fr 25fr;
}